hite;\n  filter: brightness(0.8);\n}\n\n.inputDiv > input:focus ~ label,\n.inputDiv > input:valid ~ label {\n  top: -20px;\n  left: 0;\n  font-size: 0.8rem;\n}\n\n.ulPriority {\n  flex: 1;\n  list-style: none;\n  display: flex;\n  gap: 1rem;\n  font-size: 0.8rem;\n  margin-bottom: 1rem;\n}\n\n.ulPriority > li {\n  flex: 1;\n  text-align: center;\n  margin: 1rem auto;\n}\n\n.ulPriority > li:hover {\n  filter: brightness(0.8);\n  transform: scale(1.05);\n  cursor: pointer;\n}\n\n.ulPriority > li:active {\n  transform: scale(1.2);\n}\n\n.checkboxPriority,\n.checkboxPriorityTask {\n  border: 0;\n  clip: rect(0 0 0 0);\n  height: 1px;\n  margin: -1px;\n  overflow: hidden;\n  padding: 0;\n  position: absolute;\n  width: 1px;\n}\n\n.checkboxPriority + label,\n.checkboxPriorityTask + label {\n  display: inline-block;\n  width: auto;\n  position: relative;\n  cursor: pointer;\n  padding: 1rem;\n  background: linear-gradient(\n    45deg,\n    rgba(94, 74, 94, 1),\n    rgba(91, 86, 95, 1),\n    rgba(35, 38, 41, 1),\n    rgba(18, 19, 18, 1)\n  );\n}\n\n.checkboxPriority:checked + label,\n.checkboxPriorityTask:checked + label {\n  font-weight: bold;\n}\n\n@keyframes spin {\n  from {\n    transform: translate(-50%, -50%) rotate(0);\n  }\n  to {\n    transform: translate(-50%, -50%) rotate(360deg);\n  }\n}\n\n/*background: linear-gradient(\n        45deg,\n        rgba(35, 38, 41, 1),\n        rgba(18, 19, 18, 1),\n        rgba(94, 74, 94, 1),\n        rgba(91, 86, 95, 1)\n      );\n      */\n\n/*.ulPriority > li:has(:checked) */\n\n.checkboxPriority:checked + label,\n.checkboxPriorityTask:checked + label {\n  transform: scale(1.05);\n  --border-radius: 1rem;\n  --border-size: 0.3rem;\n  --border-bg: conic-gradient(\n    rgb(62, 135, 207),\n    white,\n    rgb(18, 68, 119),\n    rgb(62, 135, 207)\n  );\n  --padding: 1rem;\n\n  position: relative;\n  overflow: hidden;\n  padding: calc(var(--padding) + var(--border-size));\n  border-radius: var(--border-radius);\n  display: inline-block;\n\n  &::before {\n    content: \"\";\n    display: block;\n    background: var(--border-bg);\n    width: calc(100% * 1.41421356237);\n    padding-bottom: calc(100% * 1.41421356237);\n    position: absolute;\n    left: 50%;\n    top: 50%;\n    transform: translate(-50%, -50%);\n    border-radius: 100%;\n    z-index: -2;\n    animation: spin 5s linear infinite;\n  }\n\n  &::after {\n    content: \"\";\n    position: absolute;\n    inset: var(--border-size);\n    background: linear-gradient(\n      45deg,\n      rgba(35, 38, 41, 1),\n      rgba(18, 19, 18, 1),\n      rgba(94, 74, 94, 1),\n      rgba(91, 86, 95, 1)\n    );\n    z-index: -1;\n    border-radius: calc(var(--border-radius) - var(--border-size));\n  }\n}\n\nlabel[for=\"highPriorityProject\"],\nlabel[for=\"highPriorityTask\"] {\n  color: rgb(253, 32, 32);\n}\n\nlabel[for=\"midPriorityProject\"],\nlabel[for=\"midPriorityTask\"] {\n  color: rgb(243, 173, 23);\n}\n\nlabel[for=\"lowPriorityProject\"],\nlabel[for=\"lowPriorityTask\"] {\n  color: rgb(10, 124, 10);\n}\n\n#taskButtonsDiv {\n  display: flex;\n  gap: 1rem;\n}\n\n#submitProject,\n#submitTask,\n#deleteTaskBtn {\n  flex: 1;\n  margin: 0 auto;\n  width: 10rem;\n  padding: 0.5rem 0 0.5rem 0;\n  border: none;\n  border-radius: 1rem;\n  background-color: rgb(22, 160, 22);\n  color: white;\n  font-weight: bold;\n  font-size: 1rem;\n}\n\n#deleteTaskBtn {\n  background-color: rgb(160, 22, 22);\n}\n\n#submitProject:hover,\n#submitTask:hover,\n#deleteTaskBtn:hover {\n  filter: brightness(0.8);\n  cursor: pointer;\n}\n\n#submitProject:active,\n#submitTask:active,\n#deleteTaskBtn:active {\n  transform: scale(1.05);\n}\n\n::backdrop {\n  background-image: linear-gradient(\n    45deg,\n    rgba(94, 74, 94, 1),\n    rgba(91, 86, 95, 1),\n    rgba(35, 38, 41, 1),\n    rgba(18, 19, 18, 1)\n  );\n  opacity: 0.75;\n}\n\n#tasksDiv {\n  display: flex;\n  flex-direction: column;\n  padding: 1rem;\n  gap: 1rem;\n}\n\n.task {\n  display: flex;\n  padding: 1rem;\n  border-radius: 1rem;\n  font-size: 1.2rem;\n  gap: 1rem;\n}\n\n.task:has(.taskDone:checked) {\n  text-decoration: line-through;\n  filter: brightness(0.5);\n}\n\n.task:hover {\n  filter: brightness(0.8);\n  cursor: pointer;\n}\n\n.task:hover:has(.taskDone:checked) {\n  filter: brightness(0.8);\n}\n\n.task:active {\n  filter: brightness(0.5);\n}\n\n.task:active:has(.taskDone:checked) {\n  filter: brightness(1);\n}\n\n.taskDate {\n  margin-left: auto;\n}\n\n.highTask {\n  background-color: rgba(253, 32, 32, 0.329);\n}\n\n.mediumTask {\n  background-color: rgba(243, 173, 23, 0.349);\n}\n\n.lowTask {\n  background-color: rgba(10, 124, 10, 0.336);\n}\n\n#addTaskBtn {\n  border: none;\n  margin-top: 1rem;\n  padding: 0.5rem;\n  border-radius: 1rem;\n  font-weight: bold;\n}\n\n#addTaskBtn:hover {\n  filter: brightness(0.8);\n  cursor: pointer;\n}\n\n#addTaskBtn:active {\n  transform: scale(1.05);\n}\n"],"sourceRoot":""}]);
